$mainColor: black;

.red {
    color: $mainColor;
}
//애니메이션 시작
.wow-enter {
    opacity: 0;
}
//애니메이션 시작 과정
.wow-enter-active{
    opacity: 1;
    transition: all 500ms;
}


//SASS 문법 함수
@mixin 함수() {
    background: #eeeeee;
    padding: 20px;
    border-radius: 5px;
    max-width: 500px;
    width: 100%;
    margin: auto;
}

.my-alert{
    @include 함수()
}
//extend문법 SASS
.my-alert2{
    @extend .my-alert;  //my-alert 복사
    background: #ffe591;
}

.my-alert p {
    margin-bottom: 0;
}

